body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: white;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

button {
	font-size: 20px;
	width: 25%;
	height: 80px;
	position: relative;
	color: white;
	outline: none;
	border: none;
	background: #444;
}

.display {
	color: white;
	text-align: right;
	font-size: 20px;
}

button:hover {
	color: black;
	background: #aaa;
	cursor: pointer;
}

.calculator {
	margin-top: 5%;
	padding: 5px;
	background: #333;
	width: 320px;
	position: relative;
}

